.header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    position: relative;
    top: 0;
    margin: 0;
    height: 5vh;
    min-height: 5vh;
    width: 100%;
    overflow: hidden;
    border-bottom: 1px solid gray;
}



.text {
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    line-height: 1.4em;
    text-transform: uppercase;
    font-size: 0.875rem;
    min-width: 200px !important;

    border:none;
    outline:none;
    color:inherit;
    -webkit-tap-highlight-color: white;
}

.user_group {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.logo {
   height: 100%;
   width: 100%; 
   color: #f67600;
   -webkit-user-select: none;
      -moz-user-select: none;
       -ms-user-select: none;
           user-select: none;
   cursor: pointer;
   margin-left: 40px;
   margin-top: 5px;
}

.header .logo .logolink  {
    font-family: "proxima-nova", sans-serif;
    font-weight: 700;
    height: 100%;
    letter-spacing: -2px;
    margin: 0;

}
.link {
    border:none;
    outline:none;
    text-decoration:none;
    color:inherit;
    -webkit-tap-highlight-color: white;
}




