.container {
    width: 100%;
    height: 100%;
}


.bar {
    background-color: #676767;
    height: 30px;
    min-height: 30px;
    width: 100%;
    margin: 0;
    color: #fff;
}

.textWrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    height: 100%;
}

.txt {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.txt:hover {
    text-decoration: underline;
    cursor: pointer;
}


